*{
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    height:100%;
    background-color: lightgoldenrodyellow;
}

html,
body,
#waffle,
.container {
    height : 100%;
    display: flex;
    justify-content: center;

}

.loader{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

.movies {
      padding-top: 70px;
      width: 80%;
}